* { text-decoration: none; list-style: none; }

a { color: white; }

body { background: #121211; color: white; }

.Home { display: -webkit-box; display: -ms-flexbox; display: flex; -ms-flex-wrap: wrap; flex-wrap: wrap; }

.movieList { width: 30%; }

.movieList .moviePoster { border: 1px solid; margin: 5% auto; width: 70%; text-align: center; -webkit-box-shadow: 10px 10px 30px 1px #3a3939; box-shadow: 10px 10px 30px 1px #3a3939; -webkit-transition: all .5s; transition: all .5s; }

.movieList .moviePoster li { margin: 0 auto; }

.movieList .moviePoster:hover { -webkit-transform: scale(1.1); transform: scale(1.1); }

.background { width: 50%; margin: 5% auto; display: -webkit-box; display: -ms-flexbox; display: flex; }

.background .title { font-size: 3rem; }

.background .genres { font-size: 2rem; }

.background .Main_img { margin: 0 5% 0 0; }

.background .desc { color: white; display: block; font-size: 1.2rem; }
